import Vue from 'vue'
import Vuex from 'vuex'
import actions from './actions'
import getters from './getters'

Vue.use(Vuex)

// 应用初始状态
const state = {
    // count: 10,
    //认证Token信息
    token: '',
    //登录用户信息
    account: {},
}

// 定义所需的 mutations
const mutations = {
    // INCREMENT(state) {
    //     state.count++
    // },
    // DECREMENT(state) {
    //     state.count--
    // }
    setToken:(state, token) => {
        state.token = token
    },
    clearToken:(state) => {
        state.token = ''
    },
    setAccount:(state, account) =>{
        state.account = account
    }
}

// 创建 store 实例
export default new Vuex.Store({
    actions,
    getters,
    state,
    mutations
})


