import axios from 'axios';
import store from '../vuex/store'
import router from '../router'

let base = 'api';

// 添加请求拦截器
axios.interceptors.request.use(config => {
    //所有请求都加入验证，除了登录
    if(config.url != `${base}/login`){
    	config.headers['Authorization'] = `Bearer ${store.state.token}`;
    }
    return config;
}, error => {
    // 对请求错误做些什么
    return Promise.reject(error);
})

// 添加响应拦截器
axios.interceptors.response.use(response => {
    // 对响应数据做点什么
    return response;
}, error => {
    // 对响应错误做点什么
    if(error.response){
    	switch(error.response.status){
    		case 401: //未授权
                store.commit('clearToken')
    			router.replace({ path: 'login'})
    	}
    }
    return Promise.reject(error);
})

export const requestLogin = params => { return axios.post(`${base}/login`, params).then(res => res.data); };

export const getUserList = params => { return axios.post(`${base}/user/list`, { params: params }); };

export const getUserListPage = params => { return axios.post(`${base}/user/listpage`, { params: params }); };

export const removeUser = params => { return axios.post(`${base}/user/remove`, { params: params }); };

export const batchRemoveUser = params => { return axios.post(`${base}/user/batchremove`, { params: params }); };

export const editUser = params => { return axios.post(`${base}/user/edit`, { params: params }); };

export const addUser = params => { return axios.post(`${base}/user/add`, { params: params }); };


